@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;1,300;1,400;1,500&display=swap);
:root{
  --darkGray:rgb(29, 34, 38);
  --dimWhite:rgba(255, 255, 255, .2);
  --black:rgb(39, 38, 38);
  --transparentBlack:rgba(39, 38, 38, .5);
}
html{
  overflow-x: hidden;
}
*{
  margin:0;
  padding:0;
}
.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
}
body{
  background-color: rgb(39, 38, 38);
  background-color: var(--black);
  color:white;
}
.navbar{
  height: 6vh;
  width: 100vw;
  background-color: rgb(39, 38, 38);
  background-color: var(--black);
  color:white;
  display: flex;
  justify-content: space-between;
  font-family: 'Open Sans', sans-serif;
  font-size: 1.1em;
  font-weight: 350;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, .5);
  position: absolute;
  top:0;
  z-index: 1;
}
.navbar-sign-in-out{
  width: 15%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 3%;
}
.navbar-sign-in-out a{
  text-decoration: none;
  color:white;
  border:1px solid white;
  padding:5px 25px;
  font-weight: 550;
  transition: all 200ms;
}
.navbar-sign-in-out a:hover{
  /* background-color: rgb(0, 0, 0); */
  color:white;
}
.username-profilebtn-wrapper{
  position: relative;
}
.view-profile-btn{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  background: none;
  border:none;
  font-size: 1.1em;
  color:white;
}
.caret{
  width: 10px;
  margin-left: 10px;
}
.view-profile-div{
  width: 100%;
  height: 0px;
  background-color: rgb(39, 38, 38);
  background-color: var(--black);
  position: absolute;
  left: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: all 150ms;
}
.profile-div-active{
  height: 30px;
  border:1px solid white;
}
.view-profile-div a{
  font-size: .9em;
  color:white;
  padding:0;
  border:0;
}
.navbar-btns{
  height: 100%;
  width: 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: 2%;
}
.navbar-btns a{
  color:white;
  text-decoration: none;
}
.hamburger-menu{
  display: none;
}
.sidebar{
  display: none;
}
.sidebar-btn{
  display: none;
}

@media screen and (max-width:1280px) and (max-height:1024px){
  html{
    max-width: 100vw;
    overflow-x: hidden;
  }
  .navbar{
    width: 100vw;
    height: 8vh;
  }
  .navbar-btns{
    width: 15%;
  }
  .navbar-sign-in-out{
    width: 30%;
  }
  .username-profilebtn-wrapper{
    min-width: 50%;
  }
  .navbar-sign-in-out a{
    padding:5px 5px;
  }
}
@media(max-width:1024px){
  .navbar{
    width: 100vw;
    border-bottom:1px solid black ;
    height: 8vh;
  }
  .navbar-sign-in-out{
    margin-right: 1%;
    width: 20%;
  }
  .navbar-btns{
    width: 20%;
  }
  .home-page-container{
    width: 80%;
  }
}
/* Surface Pro 7 */
@media screen and (max-width:912px) and (max-height:1368px) {
  .navbar a, .navbar p{
    font-size: 1.5em;
  }
  .navbar-btns{
    width: 35%;
  }
  .navbar-sign-in-out{
    width: 30%;
    margin-right: 5%;
  }
  .navbar-btns{
    width: 30%;
  }
  .username-wrapper{
    min-width: 40%;
  }
}
/* Surface Duo */
@media screen and (max-width:540px) and (max-height:720px){
  /* This class is used to disable the scroll bar on the html element */
  .disableScroll{
    overflow-y:hidden;
  }
  .navbar-btns a:last-of-type{
    display: none;
  }
  .navbar{
    width: 100vw;
    height: 7vh;
  }
  .navbar-sign-in-out{
    display: none;
  }
  .navbar a, .navbar p{
    font-size: 1em;
  }
  .hamburger-menu{
    height: 22px;
    width: 25px; 
    display: block;
  }
  .hamburger-menu ul{
    height: 100%;
    list-style: none;
    margin:0;
    padding:0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .hamburger-menu li{
    height: 4px;
    margin:1px;
    width: 100%;
    background-color: white;
  }
  /* The button that toggles the sidebar */
  .sidebar-btn{
    width: 50px;
    height: 50px;
    margin-right: 1%;
    background: none;
    border:none;
    align-self: center;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .sidebar{
    height: 93vh;
    width: 70%;
    position: absolute;
    right: 0px;
    top:7vh;
    background-color: rgb(39, 38, 38);
    background-color: var(--black);
    box-shadow: 0px 1px 3px white;
    font-family: 'Open Sans', sans-serif;
    z-index: 5;
    display: none;
    flex-direction: column;
    justify-content: space-between;
  }
  .sidebar-btns{
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: auto;
    margin-left: 0;
    margin-right: 0;
  }
  .sidebar-btns a{
    color:white;
    text-decoration: none;
    text-align: center;
    padding:10px 15px;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    background-color: rgba(255, 255, 255, .2);
    background-color: var(--dimWhite);
  }
  .sidebar-sign-in-out{
    height: 10%;
    width: 95%;
    margin:auto;
    margin-bottom: 0;
    /* background-color: red; */
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .sidebar-sign-in-out a{
    color:white;
    text-decoration: none;
    border:1px solid white;
    padding:5px 10px;
    margin-left: 5px;
  }
  .sidebar-sign-in-out p{
    margin-right: 5px;
  }
  .active{
    display: flex;
  }
}
