.home-page-container{
    width: 50%;
    margin:auto;
    margin-top:8vh;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .home-page-container h1{
    margin-bottom: 5%;
  }
  .post{
    width: 80%;
    margin-bottom:5%;
    background-color: var(--darkGray);
    color:white;
    display: flex;
    flex-direction: column;
    align-items:center;
    position:relative;
    border: 1px solid rgba(0, 0, 0, .5);
    border-radius: 5px;
    box-shadow: 1px 1px 2px rgba(0, 0, 0, .5);
  }
  .author-details-wrapper{
    width: 100%;
    padding:1% 0;
    font-size: 1.3em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .author-name-profile-pic-wrapper{
    width: 30%;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin:1% 0 1% 2%;
    overflow: hidden;
  }
  .post-pfp{
    height: 30px;
    border-radius: 50%;
  }
  .more-options-btn-wrapper{
    margin-right: 10px;
    position:relative;
  }
  .more-options-btn{
    height: 20px;
    background: none;
    border:none;
    border-radius: 5px;
    cursor: pointer;
    color:white;
    font-size: 1.5em;
    display: flex;
    align-items: center;
    transition: all 250ms;
  }
  .more-options-btn:hover{
    background-color: var(--dimWhite);
  }
  .more-options-btn p{
    margin-top:5px
  }
  .more-options{
    width: 120px;
    height:0;
    overflow: hidden;
    position:absolute;
    left:50%;
    top:150%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    background-color: var(--darkGray);
    transition: all 250ms;
  }
  .active-more-options{
    height:71px;
    border:1px solid white;
  }
  .more-options-ul{
    width: 100%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding:0;
    margin:0;
  }
  .more-options-btns{
    width: 120px;
    display: flex;
    align-items: center;
    font-size: 1em;
    color:white;
    border:none;
    background-color: var(--darkGray);
    padding:5px 0;
    cursor: pointer;
    transition: all 250ms;
  }
  .more-options-btns:hover{
    background-color: var(--dimWhite);
  }
  .delete-btn{
    border-bottom:1px solid white;
  }
  .edit-btn:hover{
    text-decoration: none;
  }
  .trash-icon, .edit-icon{
    height: 20px;
    cursor: pointer;
    margin:0 10px;
  }
  .title-wrapper{
    width: 100%;
    padding:2% 0;
    border-top:1px solid black;
    border-bottom:1px solid black;
    margin-bottom: 5%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .blog-wrapper{
    width: 90%;
    height: 90%;
    margin-bottom:5%;
    position: relative;
  }
  .blog-content{
    padding:10px;
  }
  .home-post-picture{
    max-width: 100%;
  }
  .blog-content:focus{
    outline:1px solid white;
  }
  .home-post-video{
    max-width: 90%;
  }
  .post-date{
    position:absolute;
    bottom:5px;
    right:5px;
  }
  .create-post-mobile{
    display: none;
  }
  .save-edit-post-btn{
    display: none;
    background: none;
    border:none;
    color:white;
    cursor: pointer;
    font-size: 1em;
    font-family: 'Open sans', sans-serif;
    margin-top:10px;
    background-color: var(--dimWhite);
    padding:5px 10px;
    border-radius: 5px;
    transition: all 200ms;
  }
  .save-edit-post-btn:hover{
    box-shadow:0 0 5px white;
  }
  .active-save-edit-post-btn{
    display: block;
  }
