.signInPage-div{
  height: 94vh;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top:6vh;
  }
  /* .gradient-div{
    width: 40%;
    height: 80vh;
    background: linear-gradient(65deg, #dcc2fd, #71b7fd, #adc2f6);
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;
  } */
  .sign-in-div{
    width: 40%;
    height: 80vh;
    /* background-color: white; */
    box-shadow: 0 0 5px white;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-radius: 8px;
    font-family: 'Open Sans', sans-serif;
    display: flex;
    align-items: center;
    flex-direction: column;
    /* background-color: red; */
  }
  .sign-in-div h2{
    font-size: 2em;
    margin-top:10%;
  }
  .sign-in-div p{
    margin-top: 1%;
  }
  .sign-in-inputs{
    height: 50%;
    width: 55%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .sign-in-inputs p{
    margin-top: 2%;
  }
  .input-fields{
    width: 100%;
    height: 40%;
    margin-top: 5%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .input-fields input{
    padding:8px 13px;
    border:1px solid rgba(0, 0, 0, 1);
    border-radius: 5px;
    font-size: 1.1em;
  }
  .sign-in-btn{
    background:linear-gradient(to right, #5fadfb, #615efa, #9a8cfc) ;
    width: 90%;
    margin:auto;
    font-size: 1.5em;
    color:white;
    border:none;
    border-radius: 8px;
    padding:12px 30px;
    cursor: pointer;
  }
  .dont-have-acc{
    width: 55%;
    margin-bottom: 1%;
    display: flex;
    justify-content: center;
  }
  .dont-have-acc a{
    margin-left: 2%;
    color:#6098fb;
  }
  .border-div{
    height: 1px;
    width: 55%;
    margin-bottom: 5%;
    background-color: white;
  }
  .google-btn{
    width: 40%;
    padding:12px 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border:1px solid white;
    border-radius: 8px;
    cursor: pointer;
    transition: all 250ms;
  }
  .google-btn:hover{
    background-color: rgba(255, 255, 255, .2);
  }
  .google-icon{
    width: 30px;
    position: inline-block;
    vertical-align: middle;
    margin-right: 10px;
  }
  .btn-text{
    position:inline-block;
    vertical-align: middle;
  }

@media(max-width:1280px) and (max-height:1024px){
    .signInPage-div{
        height: 92vh;
        width: 100vw;
        margin-top: 8vh;
        /* background-color: cyan; */
      }
      .sign-in-div{
        height: 85%;
        width: 55%;
        margin-top: 0;
        border-radius: 8px;
        box-shadow: 2px 2px 5px black;
        align-items: center;
        justify-content: space-around;
      }
      .sign-in-div h2{
        margin-top:5%;
      }
      .input-fields{
        height: 40%;
      }
      .sign-in-btn{
        margin-top: 10%;
        margin-bottom: 0;
      }
      .border-div{
        margin-top:0;
        margin-bottom:5%;
      }
      .google-btn{
        margin-bottom: 2%;
      }
      .gradient-div{
        display: none;
      }
}
/* Surface Pro 7 */
@media screen and (max-width:912px) and (max-height:1368px) {
    .signInPage-div{
        height: 92vh;
        margin-top: 8vh;
      }
      .sign-in-div{
        height: 75%;
        width: 80%;
        justify-content: space-around;
      }
      .sign-in-div h2{
        font-size: 2.5em;
      }
      .sign-in-div p{
        font-size: 1.4em;
      }
      .input-fields input{
        font-size: 1.5em;
      }
      .sign-in-btn{
        font-size: 2em;
        margin-top: 10%;
      }
      .border-div{
        margin-top:0;
      }
      .google-btn{
        margin-bottom: 5%;
      }
}
/* Surface Duo */
@media screen and (max-width:540px) and (max-height:720px){
    .signInPage-div{
        height: 93vh;
        margin-top: 7vh;
      }
      .sign-in-div{
        height: 95%;
        width: 95%;
      }
      .sign-in-div h2{
        font-size: 2em;
      }
      .sign-in-div p{
        font-size: 1em;
      }
      .input-fields{
        width: 70vw;
      }
      .input-fields input{
        font-size: 1.1em;
      }
      .sign-in-inputs{
        width: 100%;
      }
      .sign-in-btn{
        width: 65vw;
        font-size: 1.3em;
        margin-bottom: -10%;
      }
      .border-div{
        margin-top: 2%;
        width: 75vw;
      }
      .google-btn{
        width: 45vw;
      }
}
/* iPhone 14 Pro Max */
@media screen and (max-width:430px) and (max-height:932px) {
    .signInPage-div{
        height: 93vh;
        width: 100vw;
        margin-top: 7vh;
      }
      .sign-in-div{
        height: 80%;
        width: 99%;
        justify-content: space-around;
      }
      .sign-in-div p{
        font-size: 1em;
      }
      .sign-in-inputs{
        width: 100%;
        margin-top: 0;
      }
      .sign-in-inputs p{
        margin-top:-6%;
      }
      .input-fields{
        width: 90vw;
        height: 45%;
        margin-bottom: 10%;
      }
      .sign-up-fields{
        height: 55%;
        margin-top: -10%;
      }
      .sign-in-btn{
        width: 80%;
        font-size: 1.5em;
      }
      .dont-have-acc{
        width: 60%;
      }
      .border-div{
        display: none;
      }
      .google-btn{
       display: none;
      }
}
/* S21FE */
@media screen and (max-width:360px) and (max-height:800px){
    .sign-in-div{
        height: 80vh;
    }
    .sign-in-div p {
      font-size: .9em;
    }
    .dont-have-acc{
        width: 90%;
        padding-left:0;
    }
}